.zd-panel {
  width: 33.33%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  background: #fff;
  z-index: 899;
}
.zd-panel .zd-panel-head {
  text-align: center;
  line-height: 30px;
  background: #ff77ff;
  color: #fff;
}
.zd-panel .zd-panel-head .fa-close {
  float: right;
  line-height: 30px;
  margin-right: 10px;
}
.zd-panel .zd-panel-body {
  height: 100%;
  padding-bottom: 30px;
  box-sizing: border-box;
  overflow-y: auto;
}
.zd-panel.fixed {
  position: fixed;
}
.zd-panel.min {
  bottom: auto;
}
.zd-panel.min .zd-panel-body {
  display: none;
}
.zd-panel.right {
  right: 0;
  left: auto;
}

.zd-process {
  position: relative;
  height: 20px;
  background: #eee;
  display: inline-block;
  width: 200px;
  margin-top: 30px;
}
.zd-process span {
  display: inline-block;
  height: 100%;
  background: #f00;
}
.zd-process span:first-child {
  background: #0f0;
}
.zd-process label {
  position: absolute;
  top: -20px;
  left: 40%;
}

.table {
  display: table;
  width: 100%;
}
.table .table-row {
  display: table-row;
}
.table .table-row .table-cell {
  display: table-cell;
}

table {
  width: 100%;
}
table td,
table th {
  text-align: center;
}
table td.left,
table th.left {
  text-align: left;
}
table td.right,
table th.right {
  text-align: right;
}
table thead input,
table thead select {
  width: 100%;
  border: none;
  border-bottom: 1px solid #555;
  height: 30px;
}
table thead .sort-desc::after {
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  content: "\f15e";
}
table thead .sort-asc::after {
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  content: "\f15d";
}

.form-ico .input-group {
  position: relative;
  line-height: 30px;
}
.form-ico .input-group input[type=text],
.form-ico .input-group input[type=email],
.form-ico .input-group input[type=tel],
.form-ico .input-group input[type=password],
.form-ico .input-group input[type=url] {
  padding-left: 30px;
  height: 30px;
  width: 100%;
  box-sizing: border-box;
}
.form-ico .input-group .fa {
  position: absolute;
  left: 10px;
  top: 9px;
}
.form-ico .input-group.error input {
  border: 1px solid #f00;
}
.form-ico .input-group.error .fa {
  color: #f00;
}
.form-ico .input-group .checkbox {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
}
.form-ico .input-group .checkbox input[type=checkbox] {
  visibility: hidden;
}
.form-ico .input-group .checkbox input[type=checkbox]:checked + label::before {
  content: "\f14a";
  color: #ff77ff;
